/*Navigation menu hover effect */
.nav-item::after {
  content: "";
  display: block;
  background: var(--green);
  width: 20px;
  height: 3px;
  position: relative;
  left: 18px;
  transition: transform 1s;
  transform: scaleX(0)
}

.nav-item:hover::after {
  display: block;
  transform: scaleX(3)
  
}


.nav-item:nth-child(1):after {
  content: "";
  display: block;
  background: var(--green);
  width: 20px;
  height: 3px;
  position: relative;
  left: 40px;
  transition: transform 1s;
  transform: scaleX(0)
}

.nav-item:nth-child(1):hover::after {
  display: block;
  transform: scaleX(5)
}

.responsiveMenu:hover {
  color: yellow;
}



